.ztree * {
  padding: 0;
  margin: 0;
  font-size: 12px;
}

.ztree {
  margin: 0;
  padding: 5px;
  color: #333
}

.ztree li {
  padding: 0;
  margin: 0;
  list-style: none;
  line-height: 25px;
  text-align: left;
  white-space: nowrap;
  outline: 0
}

.ztree li ul {
  margin: 0;
  padding: 0 0 0 18px
}

.ztree li ul.line {
  //background: url(./img/line_conn.gif) 0 0 repeat-y;
}

.ztree li a {
  padding: 1px 3px 0 0;
  margin: 0;
  cursor: pointer;
  //height: 17px;
  color: #333;
  background-color: transparent;
  text-decoration: none;
  vertical-align: top;
  display: inline-block
}

